<template>
  <el-button @click="goBack">返回配置列表</el-button>
  <el-button @click="getOciCfgDetails" :loading="flushLoading">刷新页面</el-button>
  <span style="margin-left: 20px;"><el-text type="primary">配置：{{ this.user.username }} —— 实例详情</el-text></span>
  <el-divider/>
  <div class="instance-list">
    <div
        v-for="instance in instanceList"
        :key="instance.ocId"
        class="instance-card"
    >
      <h3>{{ instance.name }}</h3>
      <p><strong>Region:</strong> {{ instance.region }}</p>
      <p><strong>Shape:</strong> {{ instance.shape }}</p>
      <div class="ip-list">
        <p><strong>Public IPs:</strong></p>
        <div
            v-for="(ip, ipIndex) in instance.publicIp"
            :key="ipIndex"
            class="ip-item"
        >
          {{ ip }}
        </div>
      </div>
      <el-button @click="handleChangeIp(instance.ocId)" >更换IP</el-button>
      <el-button @click="handleStopChangeIp(instance.ocId)" v-if="instance.enableChangeIp === 1">停止更换IP任务</el-button>
    </div>
  </div>
  <el-dialog
      v-model="dialogVisible"
      title="可指定多个 CIDR 网段，回车保存，传空则随机更换一个IP"
      width="40%"
      @close="dialogVisible = false"
  >
    <el-tag
        v-for="tag in changeIpParams.cidrList"
        :key="tag"
        closable
        @close="handleClose(tag)"
    >
      {{ tag }}
    </el-tag>
    <el-input
        v-if="inputVisible"
        ref="InputRef"
        v-model="inputValue"
        class="w-20"
        @keyup.enter="handleInputConfirm"
        @blur="handleInputConfirm"
    />
    <el-button v-else class="button-new-tag" size="small" @click="showInput">
      + New CIDR
    </el-button>
    <template #footer>
      <el-button @click="dialogVisible = false">取消</el-button>
      <el-button type="primary" @click="changeIp" :loading="changeIpLoading">新增更换IP任务</el-button>
    </template>
  </el-dialog>
</template>

<script>
import {ElMessage} from "element-plus";

export default {
  data() {
    return {
      flushLoading: false,
      changeIpLoading: false,
      dialogVisible: false,
      inputVisible: false,
      inputValue: '',
      user: {},
      changeIpParams: {
        ociCfgId: '',
        instanceId: '',
        cidrList: []
      },
      instanceList: [] // 从后端获取的 instanceList 数据
    };
  },
  mounted() {
    this.user = JSON.parse(this.$route.query.row || '{}')
    this.changeIpParams.ociCfgId = this.user.id
    this.getOciCfgDetails()
  },
  methods: {
    goBack() {
      this.$router.back(); // 返回上一个页面
    },
    handleClose(tag) {
      const index = this.changeIpParams.cidrList.indexOf(tag);
      if (index !== -1) {
        this.changeIpParams.cidrList.splice(index, 1);
      }
    },
    handleStopChangeIp(ocId){
      const confirmed = window.confirm('确定要停止更换IP任务吗?');
      if (confirmed) {
        this.stopChangeIp(ocId);
      } else {
        this.$message.info('已取消');
      }
    },
    stopChangeIp(ocId){
      this.$axios.post('/oci/stopChangeIp', {instanceId: ocId})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
            this.getOciCfgDetails()
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    showInput() {
      this.inputVisible = true;
      this.$nextTick(() => {
        this.$refs.InputRef.focus();
      });
    },
    handleInputConfirm() {
      if (this.inputValue && !this.changeIpParams.cidrList.includes(this.inputValue)) {
        this.changeIpParams.cidrList.push(this.inputValue);
      }
      this.inputValue = '';
      this.inputVisible = false;
    },
    async getOciCfgDetails() {
      this.instanceList = []
      this.flushLoading = true
      await this.$axios.post('/oci/details', {id: this.changeIpParams.ociCfgId})
          .then(response => {
            if (response.data.success) {
              this.instanceList = response.data.data.instanceList
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
              this.flushLoading = false
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    handleChangeIp(id) {
      this.dialogVisible = true
      this.changeIpParams.instanceId = id
    },
    changeIp() {
      this.changeIpLoading = true
      this.$axios.post('/oci/changeIp', {...this.changeIpParams})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
              this.changeIpLoading = false
              this.dialogVisible = false
              this.getOciCfgDetails()
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
    }
  },

};
</script>

<style>
.instance-list {
  display: flex;
  flex-wrap: wrap;
}

.instance-card {
  border: 1px solid #ddd;
  padding: 16px;
  margin: 8px;
  width: 250px;
  border-radius: 8px;
}

.ip-item {
  display: flex;
  align-items: center;
  margin: 4px 0;
}

button {
  margin-left: 8px;
  padding: 4px 8px;
  cursor: pointer;
}
</style>
