<template>
  <div>
    <el-button type="success"
               style="margin-bottom: 20px;"
               @click="fetchUsers">
      刷新
    </el-button>

    <el-button
        type="primary"
        @click="dialogVisible = true"
        style="margin-left: 20px;margin-bottom: 20px;"
    >
      新增配置
    </el-button>

    <el-input
        v-model="pageParams.keyword"
        placeholder="模糊搜索名称/区域"
        @input="fetchUsers"
        clearable
        style="margin-left: 20px; width: 200px; margin-bottom: 20px;"
    />
    <el-select
        v-model="pageParams.isEnableCreate"
        @change="fetchUsers"
        placeholder="开机任务状态筛选"
        style="margin-left: 20px; width: 180px; margin-bottom: 20px;"
    >
      <el-option label="全部" :value="null"></el-option>
      <el-option label="仅显示执行开机任务中" :value="1"></el-option>
      <el-option label="无开机任务" :value="0"></el-option>
    </el-select>

    <el-button
        type="danger"
        @click="showDeleteSelected"
        :disabled="!selectedUsers.length"
        style="margin-left: 20px; margin-bottom: 20px;"
    >
      批量删除
    </el-button>

    <el-button
        type="warning"
        @click="createBatch"
        :disabled="!selectedUsers.length"
        style="margin-left: 20px; margin-bottom: 20px;"
    >
      批量开机
    </el-button>

    <!-- 可滑动的表格 -->
    <div style="overflow-x: auto;">
      <el-table
          :data="users"
          style="width: 100%; min-width: 800px;"
          v-loading="loading"
          @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="55"/>
        <el-table-column fixed prop="username" label="名称"/>
        <el-table-column label="区域">
          <template #default="{ row }">
            <el-tag type="primary">
              {{ row.region }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column label="开机任务状态">
          <template #default="{ row }">
            <el-tag :type="row.enableCreate === 1 ? 'success':'info'">
              {{ row.enableCreate === 1 ? '执行开机任务中' : '无开机任务' }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column sortable prop="createTime" label="创建时间">
          <template #default="{ row }">
            <el-tag type="info">
              {{ row.createTime }}
            </el-tag>
          </template>
        </el-table-column>
        <!-- 缩小的操作列 -->
        <el-table-column fixed="right" label="操作" width="250">
          <template #default="{ row }">
            <el-button @click="viewDetails(row)" type="primary" link>详情</el-button>
            <el-button @click="createInstance(row)" type="warning" link>开机</el-button>
            <el-button @click="handleStopCreate(row)" link
                       :type="row.enableCreate === 0 ? 'info' : 'danger'"
                       :disabled="row.enableCreate === 0">停止所有开机任务
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 分页控制 -->
    <el-pagination
        @current-change="handlePageChange"
        @size-change="handleSizeChange"
        v-model:current-page="pageParams.currentPage"
        v-model:page-size="pageParams.pageSize"
        :page-sizes="[5, 10, 20, 50]"
        :total="total"
        layout="total, sizes, prev, pager, next, jumper"
        style="margin-top: 20px;"
    />
  </div>
  <el-dialog
      v-model="dialogVisible"
      title="新增配置"
      width="30%"
      @close="dialogVisible = false"
  >
    <el-form ref="formRef" :model="addCfgFormData" :rules="formRules" label-width="80px">
      <el-form-item label="配置名称" prop="username">
        <el-input v-model="addCfgFormData.username" placeholder="请输入配置名称"/>
      </el-form-item>
      <el-form-item label="配置内容" prop="ociCfgStr">
        <el-input
            v-model="addCfgFormData.ociCfgStr"
            type="textarea"
            :placeholder="placeholderText"
            rows="6"
        />
      </el-form-item>
    </el-form>
    <template #footer>
      <el-button @click="dialogVisible = false">取消</el-button>
      <el-button type="primary" @click="addUser" :disabled="!addCfgFormData.disabled" :loading="addLoading">新增
      </el-button>
    </template>
  </el-dialog>
</template>

<script>
import {ElTable, ElTableColumn, ElPagination, ElInput, ElButton, ElMessage} from 'element-plus';

export default {
  components: {
    ElTable, ElTableColumn, ElPagination, ElInput, ElButton
  },
  data() {
    return {
      users: [],
      pageParams: {
        keyword: '',
        currentPage: 1,
        pageSize: 5,
        isEnableCreate: null
      },
      total: 2,
      selectedUsers: [],
      loading: true,
      addLoading: false,
      dialogVisible: false,
      addCfgFormData: {
        disabled: false,
        username: '',
        ociCfgStr: ''
      },
      formRules: {
        username: [
          {required: true, message: '配置名称不能为空', trigger: 'blur'},
          {min: 1, message: '配置名称至少为 1 个字符', trigger: 'blur'},
        ],
        ociCfgStr: [
          {required: true, message: '配置内容不能为空', trigger: 'blur'},
          {min: 10, message: '配置内容至少为 10 个字符', trigger: 'blur'},
        ],
      },
      placeholderText: "user=xxxxxx\nfingerprint=xxxxxx\ntenancy=xxxxxx\nregion=sa-saopaulo-1\nkey_file=br.pem\n"
    };
  },
  watch: {
    // 监听表单数据变化，触发验证以更新按钮状态
    addCfgFormData: {
      handler() {
        this.validateForm();
      },
      deep: true,
    },
  },
  methods: {
    async fetchUsers() {
      this.loading = true;
      this.$axios.post('/oci/userPage', {...this.pageParams})
          .then(response => {
            this.users = response.data.data.records;
            this.total = response.data.data.total;
          })
          .catch(error => {
            console.error('Error:', error)
          })
      this.loading = false;
    },
    handlePageChange(page) {
      this.pageParams.currentPage = page;
      this.fetchUsers();
    },
    handleSizeChange(size) {
      this.pageParams.currentPage = 1;
      this.pageParams.pageSize = size;
      this.fetchUsers();
    },
    handleSelectionChange(val) {
      this.selectedUsers = val;
    },
    async addUser() {
      this.addLoading = true
      await this.$axios.post('/oci/addCfg', {...this.addCfgFormData})
          .then(response => {
            if (response.data.success) {
              this.addLoading = false
              this.dialogVisible = false
              this.addCfgFormData.username = null
              this.addCfgFormData.ociCfgStr = null
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            } else {
              this.addLoading = false
              this.addCfgFormData.ociCfgStr = null
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
            this.fetchUsers();
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    viewDetails(row) {
      this.$router.push({
        path: '/dashboard/ociInstanceInfo',
        query: {row: JSON.stringify(row)},
      });
    },
    createInstance(row) {
      this.$router.push({
        path: '/dashboard/createInstance',
        query: {row: JSON.stringify(row)},
      });
    },
    createBatch(){
      this.$router.push({
        path: '/dashboard/ociCreateInstanceBatch',
        query: {row: JSON.stringify(this.selectedUsers)},
      });
    },
    handleStopCreate(row) {
      const confirmed = window.confirm('确定要停止开机任务吗?');
      if (confirmed) {
        this.stopCreate(row);
      } else {
        this.$message.info('已取消');
      }
    },
    stopCreate(row) {
      this.$axios.post('/oci/stopCreate', {userId: row.id})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
            this.fetchUsers();
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    showDeleteSelected() {
      const confirmed = window.confirm('确定要删除这些配置吗?');
      if (confirmed) {
        this.deleteSelected();
      } else {
        this.$message.info('取消删除');
      }
    },
    deleteSelected() {
      const ids = this.selectedUsers.map(user => user.id);
      this.$axios.post('/oci/removeCfg', {idList: ids})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
            this.fetchUsers();
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    validateForm() {
      this.$refs.formRef.validate((valid) => {
        this.addCfgFormData.disabled = valid;
      });
    },
  },
  mounted() {
    this.fetchUsers();
  },
};
</script>

