<template>
  <el-container class="dashboard-container">
    <!-- 侧边栏 -->
    <el-aside width="200px" class="sidebar">
      <div class="logo" style="text-align: center">oci-helper</div>
      <el-scrollbar>
        <el-menu
            :default-active="activeMenu"
            @select="handleSelect"
            router
            class="sidebar-menu"
            background-color="transparent"
        >
          <el-menu-item index="home">主页</el-menu-item>
          <el-menu-item index="user">配置列表</el-menu-item>
          <el-menu-item index="createTask">开机任务列表</el-menu-item>
        </el-menu>
      </el-scrollbar>
    </el-aside>

    <!-- 主内容区域 -->
    <el-container>
      <el-header class="header">
        <span></span>
        <button @click="showLogoutConfirm" class="logout-button">退出登录</button>
      </el-header>
      <el-main>
        <router-view></router-view> <!-- 显示区域，用于切换内容 -->
      </el-main>
      <el-footer style="text-align: center;">
        <el-link href="https://github.com/Yohann0617" target="_blank">© Yohann All Rights Reserved</el-link>
      </el-footer>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data() {
    return {
      activeMenu: 'home'
    };
  },
  methods: {
    handleSelect(index) {
      this.activeMenu = index;
      this.$router.push(`/dashboard/${index}`);
    },
    showLogoutConfirm() {
      // 使用原生 confirm 弹框进行确认
      const confirmed = window.confirm('确定要退出登录吗?');
      if (confirmed) {
        this.logout(); // 用户确认退出
      } else {
        this.$message.info('取消退出'); // 用户取消退出
      }
    },
    logout() {
      sessionStorage.removeItem('token');
      this.$router.push('/login');
    }
  }
};
</script>

<style scoped>
.dashboard-container {
  height: 100vh;
}

.sidebar {
  background-color: #409eff;
  color: #fff;
  display: flex;
  flex-direction: column;
}

.logo {
  font-size: 20px;
  font-weight: bold;
  padding: 20px;
  color: #fff;
}

.sidebar-menu ::v-deep .el-menu-item {
  color: #ffffff;
}

.sidebar-menu ::v-deep .el-menu-item:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.sidebar-menu ::v-deep .el-menu-item.is-active {
  background-color: rgba(255, 255, 255, 0.2);
  font-weight: bold;
}

.header {
  display: flex;
  justify-content: space-between;
  padding: 0 20px;
  background-color: #409eff;
  color: #fff;
  align-items: center;
  height: 66px;
}

.logout-button {
  background: none;
  border: none;
  color: #fff;
  cursor: pointer;
}

.logout-button:hover {
  color: #ff4d4f;
}
</style>
