<template>
  <div class="login-container">
    <el-card class="login-card">
      <h2 class="login-title">oci-helper Login</h2>
      <el-form @submit.prevent="login" @keydown.enter="login">
        <el-form-item label="账号" label-width="100px">
          <el-input v-model="loginParams.account" placeholder="请输入账号"/>
        </el-form-item>
        <el-form-item label="密码" label-width="100px">
          <el-input show-password v-model="loginParams.password" placeholder="请输入密码" type="password"/>
        </el-form-item>
        <el-form-item>
          <div class="login-button-container">
            <el-button type="primary" @click="login">Login</el-button>
          </div>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import {ElMessage} from "element-plus";

export default {
  data() {
    return {
      loginParams: {
        account: '',
        password: ''
      },
    };
  },
  methods: {
    async login() {
     await this.$axios.post('/oci/login', {...this.loginParams})
          .then(response => {
            if (response.data.success) {
              this.$router.push('/dashboard');
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
              sessionStorage.setItem('token', response.data.data);
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
          })
          .catch(error => {
            console.error('Error:', error)
          })
    }
  }
};
</script>

<style scoped>
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f0f2f5;
}

.login-card {
  width: 400px;
  padding: 20px;
}

.login-title {
  text-align: center;
  margin-bottom: 20px;
}

.login-button-container {
  text-align: center;
}

</style>
